import { useState, ChangeEvent, FormEvent, useRef } from "react";
import axios from "axios";
import MainSearchSection from "../Components/MainSearchSection";
import SearchIcon from "../../assets/Icons/Search";
import QuestionPhoto from "../../assets/Images/Screenshot_20250124_165516_com.qualityconnect 2.png";
import "../Style/Support.css";

import SupportQuestion from "../Components/SupportQuetion";
import { ApiHost } from "../../API";
import ArrowDown from "../../assets/Icons/ArrowDown";

// تعريف نوع البيانات للـ form
interface FormData {
  fullname: string;
  email: string;
  phone_number: string;
  description: string;
}

const Support = () => {
  const [formData, setFormData] = useState<FormData>({
    fullname: "",
    email: "",
    phone_number: "",
    description: "",
  });
  const [searchInput, setSearchInput] = useState("")
  const [loading, setLoading] = useState<boolean>(false);
  const [successMessage, setSuccessMessage] = useState<string>("");
  const [errorMessage, setErrorMessage] = useState<string>("");
  const [DataSearch, setDataSearch] = useState<any>([]);
  const [FormDataSearch, setFromDataSearch] = useState([]);
  const faqSectionRef = useRef<HTMLDivElement | null>(null);

  const handleChangeSearch = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { value } = e.target;

    setSearchInput(value)
    // عند ترك الحقل فارغًا، استرجع جميع البيانات أو فرّغ النتائج حسب الحاجة
    if (!value.trim()) {
      setDataSearch([]);
      return;
    }

    const newData = faqData.filter(data =>
      data.question.toLowerCase().includes(value.toLowerCase())
    );
    setDataSearch(newData)

  };

  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (
      !formData.fullname ||
      !formData.email ||
      !formData.phone_number ||
      !formData.description
    ) {
      alert("يرجى ملء جميع الحقول");
      return;
    }

    setLoading(true);
    setErrorMessage("");
    setSuccessMessage("");

    try {
      const response = await axios.post(`${ApiHost}/api/Report/add`, {
        fullname: formData.fullname,
        email: formData.email,
        phone_number: formData.phone_number,
        description: formData.description,
      });

      if (response.data.message === "تمت إضافة التقرير بنجاح.") {
        setSuccessMessage("تم إرسال الرسالة بنجاح!");
        setFormData({
          fullname: "",
          email: "",
          phone_number: "",
          description: "",
        });
      } else {
        setErrorMessage("حدث خطأ، يرجى المحاولة مرة أخرى.");
      }
    } catch (error) {
      console.error("Error:", error);
      setErrorMessage("حدث خطأ أثناء إرسال البيانات.");
    } finally {
      setLoading(false);
    }
  };

  const handelSearch = () => {
    setFromDataSearch(DataSearch)
    setDataSearch([])
    if (faqSectionRef.current) {
      faqSectionRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }
  const handelSubmitSearch = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFromDataSearch(DataSearch)
    setDataSearch([])
    if (faqSectionRef.current) {
      faqSectionRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }

  const faqData = [
    {
      question: "هل محفظة شلن آمنة؟",
      answer:
        "نعم، محفظة شلن تستخدم أحدث تقنيات الأمان مثل التشفير والمصادقة الثنائية لحماية بيانات المستخدمين.",
    },
    {
      question: "كيف يمكنني إيداع الأموال في محفظة شلن؟",
      answer:
        "يمكنك إيداع الأموال من خلال التحويل البنكي، البطاقات المصرفية، أو من خلال وكلائنا المعتمدين.",
    },
    {
      question: "هل يمكنني سحب الأموال من المحفظة إلى حسابي البنكي؟",
      answer:
        "نعم، يمكنك سحب الأموال إلى حسابك البنكي بسهولة من خلال قسم السحب في التطبيق.",
    },
    {
      question: "ما هي العملات المدعومة في محفظة شلن؟",
      answer:
        "محفظة شلن تدعم العملات المحلية مثل الريال اليمني بالإضافة إلى العملات الرقمية مثل USDT وBitcoin.",
    },
    {
      question: "هل هناك رسوم على التحويلات داخل المحفظة؟",
      answer:
        "لا، التحويلات داخل المحفظة مجانية، ولكن قد يتم فرض رسوم رمزية على التحويلات إلى الحسابات البنكية.",
    },
  ];


  return (
    <div className="support">
      <MainSearchSection
        imageSrc={QuestionPhoto}
        title="الدعم الفني"
        className="search"
        dir={false}
      >
        <p className="section-description">
          إذا كان لديك أي سؤال لا تتردد في السؤال
        </p>

        <form onSubmit={handelSubmitSearch} className="form-search">
          <div className="input-group">
            <input
              type="text"
              placeholder="كيف نقدر نساعدك ؟"
              onChange={handleChangeSearch}
              name="search"
              value={searchInput}
            />

            <SearchIcon />
          </div>
          <ul className="search-list">
            {DataSearch.map((item: any, index: any) => (
              <li onClick={handelSearch} key={index}>{item?.question}</li>
            ))}
          </ul>
        </form>
      </MainSearchSection>

      <div ref={faqSectionRef} className="qution-support-section">
        <div className="section-header">
          <p className="description">(FAQs) الأسئلة الشائعة</p>
          <h1 className="title">معلومات قد تفيدك</h1>
        </div>
      </div>
      <ul  className="support-question">
        {(FormDataSearch.length ? FormDataSearch : faqData).map(
          (item: any, index) => (
            <SupportQuestion
              key={index}
              question={item.question}
              answer={item.answer}
            />
          )
        )}
      </ul>
      <div className="back-quetion">
        {FormDataSearch.length ? <button className="back-quetion-btn" onClick={() => {
          setSearchInput("")
          setFromDataSearch([])
        }}>إظهار جميع الأسئلة
          <ArrowDown />
        </button> : ""}


      </div>


      <div className="contact">
        <div className="section-header">
          <p className="description">للتواصل مع خدمة العملاء</p>
          <h1 className="title">
            <span>أكتب رسالتك في نموذج الإرسال</span>
          </h1>
        </div>
        <form className="form-contect" onSubmit={handleSubmit}>
          <div className="input-group">
            <label>الاسم الكامل</label>
            <input
              type="text"
              placeholder="محمد علي محمد"
              name="fullname"
              value={formData.fullname}
              onChange={handleChange}
            />
          </div>
          <div className="input-group">
            <label>البريد الإلكتروني</label>
            <input
              type="email"
              placeholder="أكتب بريدك الالكتروني..."
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
          </div>
          <div className="input-group">
            <label>رقم الجوال</label>
            <input
              type="text"
              placeholder="+967 000 000 000"
              name="phone_number"
              value={formData.phone_number}
              onChange={handleChange}
            />
          </div>
          <div className="input-group">
            <label>محتوى الرسالة</label>
            <textarea
              placeholder="كيف يمكننا أن نساعدك..."
              name="description"
              value={formData.description}
              onChange={handleChange}
            />
          </div>
          <button type="submit" disabled={loading}>
            {loading ? "جاري الإرسال..." : "إرسال الرسالة"}
          </button>
        </form>
        {successMessage && <p>{successMessage}</p>}
        {errorMessage && <p>{errorMessage}</p>}
      </div>
    </div>
  );
};

export default Support;
