import { ReactNode, useEffect } from "react";
import "../../Shared/Style/SectionTwo.css";
import Aos from "aos";
import "aos/dist/aos.css"; // استيراد CSS الخاصة بـ AOS
import PhoneIcon from "../../assets/Icons/PhoneIcon";
import ArrowLineIcon from "../../assets/Icons/ArrowLineIcon";
import Cirecl from "../../assets/Icons/Cirecl";


interface MainSearchSectionProps {
  imageSrc: string;
  title: string;
  className?: string;
  children: ReactNode;
  dir: boolean;
}

const MainSearchSection = ({
  imageSrc,
  title,
  className = "",
  children,
  dir,
}: MainSearchSectionProps) => {
      useEffect(() => {
        Aos.init({
          duration: 800,
          easing: "ease-in-out", 
          once: true,
        });
      }, []);
  return (
    <section className={`section-two-container ${dir ? "section-right":"section-left"} ${className}`}>
      {!dir &&(
        <>
          <div  className="content-wrapper">
            <h1 className="section-title">{title}</h1>
            {children}

          </div>
          <div  className="image-wrapper">
            <img  src={imageSrc} alt="Section Image" />
            <PhoneIcon/>
            <ArrowLineIcon/>
            <div data-aos="fade-right" className="item">
              <span><Cirecl/></span>
              <p  >نسعد في خدمتك وتسهيل حياتك المالية أكثر</p>
            </div>
          </div>
        </>
      )}
    </section>
  );
};

export default MainSearchSection;
