import { useState } from "react";
import ArrowDown from "../../assets/Icons/ArrowDown";
interface SupportQuestionProps {
    question:string;
    answer:string;
}
const SupportQuestion = ({ question, answer }:SupportQuestionProps) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <li className="question-item">
      <div onClick={() => setIsOpen((prev) => !prev)}  className="header">
        <p>{question}</p>
        <div className={`icon ${isOpen ? "active" : ""}`}>
          <ArrowDown />
        </div>
      </div>
      <p className={`question-description ${isOpen ? "active" : ""}`}>
        {answer}
      </p>
    </li>
  );
};


export default SupportQuestion;
